import { FunctionComponent, forwardRef, PropsWithoutRef } from 'react';

import { ForwardProps, useI18n } from '@pega/cosmos-react-core';

import Message from './Message';
import { TypeIndicatorProps } from './Chat.types';

const TypeIndicator: FunctionComponent<TypeIndicatorProps & ForwardProps> = forwardRef(
  function TypeIndicator(
    props: PropsWithoutRef<TypeIndicatorProps>,
    ref: TypeIndicatorProps['ref']
  ) {
    const { avatarInfo, message, senderId, senderType, agentVariant, ...restProps } = props;
    const t = useI18n();

    const ariaLabel = t('is_typing', [avatarInfo.name, message || '']);
    return (
      <Message
        {...restProps}
        senderType={senderType}
        senderId={senderId}
        direction='in'
        avatarInfo={avatarInfo}
        agentVariant={agentVariant}
        typing
        ref={ref}
        message={message}
        aria-label={ariaLabel}
      />
    );
  }
);

export default TypeIndicator;
